"use client";

import { useState } from "react";
import {
  Box,
  Button,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Modal,
  Rating,
} from "@mui/material";
import styles from "./main.module.css";

// 1. Flower Type
type Flower = {
  name: string;
  price: string;
  img: string;
};

// 2. Flower List
const flowers: Flower[] = [
  { name: "ვარდი", price: "15$", img: "roses.jpg" },
  { name: "შროშანი", price: "10$", img: "lily.jpg" },
  { name: "ტიტა", price: "12$", img: "tulip.jpg" },
  { name: "გვირილა", price: "11$", img: "daisy.jpg" },
  { name: "ქრიზანთემა", price: "9$", img: "Chrysanthemum.jpg" },
  { name: "მიხაკი", price: "18$", img: "Carnation.jpg" },
  { name: "ლავანდა", price: "6$", img: "Lavender.jpg" },
  { name: "ორქიდეა", price: "11$", img: "Orchid.jpg" },
  { name: "მზეაყვავილა", price: "12$", img: "Sunflower.jpg" },
];

const MainContent = () => {
  const [open, setOpen] = useState(false);
  const [selectedFlower, setSelectedFlower] = useState<Flower | null>(null);

  const handleOpen = (flower: Flower) => {
    setSelectedFlower(flower);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setSelectedFlower(null);
  };

  return (
    <>
      <Box className={styles.main}>
        {flowers.map((item, index) => (
          <Card
            key={index}
            onClick={() => handleOpen(item)}
            sx={{
              width: 300,
              height: 400,
              borderRadius: 4,
              boxShadow: 6,
              transition: "0.3s ease",
              cursor: "pointer",
              "&:hover": {
                transform: "scale(1.03)",
                boxShadow: 10,
              },
            }}
          >
            <CardMedia
              component="img"
              height="230"
              image={item.img}
              alt={item.name}
            />

            <CardContent>
              <Typography
                variant="h6"
                gutterBottom
                sx={{ fontWeight: "bold", fontFamily: "Georgia, serif" }}
              >
                {item.name}
              </Typography>

              <Box
                sx={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                }}
              >
                <Typography
                  variant="h6"
                  sx={{
                    color: "crimson",
                    fontWeight: "bold",
                    fontSize: "1.2rem",
                  }}
                >
                  {item.price}
                </Typography>

                <Button
                  variant="outlined"
                  sx={{
                    borderColor: "pink",
                    color: "pink",
                    textTransform: "none",
                    fontWeight: "bold",
                    "&:hover": {
                      borderColor: "#ff4081",
                      backgroundColor: "#ffe6f0",
                      color: "#ff4081",
                    },
                  }}
                >
                  Buy
                </Button>
              </Box>
            </CardContent>
          </Card>
        ))}
      </Box>

      <Modal open={open} onClose={handleClose}>
        <Box
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            bgcolor: "white",
            p: 4,
            borderRadius: 7,
            boxShadow: 24,
            width: 700,
            maxHeight: "80vh",
            overflowY: "auto",
          }}
        >
          {selectedFlower && (
            <Box sx={{ display: "flex", flexDirection: { xs: "column", sm: "row" }, gap: 3 }}>
              <Box sx={{ flex: 1 }}>
                <Box
                  component="img"
                  src={selectedFlower.img}
                  alt={selectedFlower.name}
                  sx={{
                    width: 350,
                    height: 400,
                    objectFit: "cover",
                  }}
                />

                <Typography variant="h6" sx={{ mt: 2, color: "crimson" }}>
                  Price: {selectedFlower.price}
                </Typography>

                <Typography sx={{ mt: 1 }}>
                  Rating:
                </Typography>
                <Rating value={4.5} precision={0.5} readOnly sx={{fontSize: 45}}/>
              </Box>

              <Box sx={{ flex: 2 }}>
                <Typography variant="h4" gutterBottom sx={{ fontWeight: "bold" }}>
                  {selectedFlower.name}
                </Typography>

                <Typography sx={{ mb: 3, color: "gray" }}>
                  This flower is perfect for any occasion, with vibrant colors and a lovely fragrance. Add your custom description here. You can expand on use cases, occasion suggestions, etc.
                </Typography>

                <Box sx={{ display: "flex", gap: 2, flexWrap: "wrap" }}>
                  <Button variant="contained" color="primary">
                    Buy Now
                  </Button>
                  <Button variant="outlined" color="secondary">
                    Add to Cart
                  </Button>
                </Box>
              </Box>
            </Box>
          )}
        </Box>
      </Modal>
    </>
  );
};

export default MainContent;
